@import "../../global.scss";

.portfolio{
    background-color: $hardBackground;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
    a{
        color: black;
        position: absolute;
        bottom: 0;
        .icon{
            font-size: 60px;
            animation: arrowBlink 1.5s infinite;
            }

            @keyframes arrowBlink {
                100%{
                opacity: 0;
                }
        }
    }
    .card{
        background-color: $softBackground;
        display: flex;
        align-items: center;
        flex-direction: column;
        border: $outerBorder;
        box-sizing: border-box;
        height: $cHeight;
        width: $cWidth;
        position: relative;
    h1{
        top: 0;
        font-size: 50px;

        @include mobile{
            font-size: 20px;
        }
    }
    ul{
        margin: 10px;
        padding: 0;
        list-style: none;
        display: flex;
        @include mobile{
            flex-wrap: wrap;
            justify-content: center;
            margin: 10px 0;
        }
        li{
            font-size: 14px;
            margin-right: 50px;
            padding: 7px;
            border-radius: 10px;
            cursor: pointer;

            &.active{
                background-color: $mainColor;
                color: white;
            }
        }
        
    }
    
 
    .container{
        width: 70%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;

        @include mobile{
            width: 100%;
        }

        .item{
            width: 33vw;
            height: 25vh;
            border-radius: 20px;
            border: 1px solid rgb(230, 230, 230);
            margin: 10px 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: white;
            position: relative;
            transition: all .5s ease;
            cursor: pointer;

            @include mobile{
                width: 100%;
            }
            
            h3{
                display: flex;
                justify-content: center;
                position: absolute;
                font-size: 20px;
            }
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                z-index: 1;
            }
            a {
                z-index: 1;
            }

            &:hover{
                background-color: $mainColor;
                img{
                    z-index: 0;
                    opacity: 0.3;
                    
                }
            }
        }
    }
}
}